import React, { useState, useRef, useMemo, createRef } from 'react';
import { useCalendarData } from '../../hooks/useCalendarData';
import { useTaskHandlers } from '../../hooks/useTaskHandlers';
import { useCalendarNavigation } from '../../hooks/useCalendarNavigation';
import { CalendarMain } from '../calendar/CalendarMain';
import { TaskBoard } from '../tasks/TaskBoard';
import { TaskForm } from '../tasks/TaskForm';
import '../../style/CalendarView.css';

export const CalendarView = () => {
  // tat principal du calendrier
  const [calendarState, setCalendarState] = useState({
    showWeekends: true,
    isFormOpen: false,
    selectedDates: null,
    selectedTask: null,
    isProcessing: false,
    currentView: 'resourceTimelineYear',
    taskboardDestination: null,
    taskOriginId: null
  });

  // Anne actuellement slectionne dans le calendrier
  const [selectedYear, setSelectedYear] = useState(new Date().getFullYear());

  // Zones de dpt pour le TaskBoard
  const dropZones = useMemo(() => [
    { id: 'todo', statusId: '1', title: ' faire' },
    { id: 'inProgress', statusId: '2', title: 'En cours' },
    { id: 'blocked', statusId: '3', title: 'En attente' },
    { id: 'done', statusId: '4', title: 'Done' }
  ], []);

  // Crer les rfrences pour les zones de dpt
  const dropZoneRefs = useRef(dropZones.map(() => createRef()));

  const calendarRef = useRef(null);
  const { tasks, setTasks, resources, holidays, statuses } = useCalendarData();

  // Grer la fermeture du formulaire
  const handleFormClose = () => {
    setCalendarState(prev => ({
      ...prev,
      isFormOpen: false,
      selectedTask: null,
      selectedDates: null,
      taskboardDestination: null,
      taskOriginId: null
    }));
  };

  // Grer le dplacement des tches entre les taskboards
  const handleMoveTask = (taskId, newStatusId) => {
    // Trouver la tche  dplacer
    const taskToMove = tasks.find(task => task.id.toString() === taskId.toString());

    if (!taskToMove) {
      console.error(`Tche avec l'ID ${taskId} non trouve`);
      return;
    }
    if (newStatusId === '2') {
      // Ouvrir le formulaire pour modifier la tche
      setCalendarState(prev => ({
        ...prev,
        isFormOpen: true,
        selectedTask: taskToMove,
        taskboardDestination: '2',
        taskOriginId: taskId
      }));
    } else {
      // Vrifier si la tche est actuellement dans le taskboard "En cours"
      const isMovingFromInProgress = (taskToMove.extendedProps?.statusId === '2' || taskToMove.statusId === '2');

      // Prparer les mises  jour
      const updates = {
        statusId: newStatusId,
        extendedProps: {
          ...taskToMove.extendedProps,
          inclusiveEndDate: null,
          exclusiveEndDate: null,
          statusId: newStatusId
        }
      };

      // Si la tche sort du taskboard "En cours", rinitialiser les dates et le propritaire
      if (isMovingFromInProgress) {
        updates.start = null;
        updates.end = null;
        updates.start_date = null;
        updates.end_date = null;
        updates.resourceId = null;
        updates.owner_id = null;
      }

      // Utiliser handleTaskUpdate du hook pour mise  jour ET persistance en base de donnes
      taskHandlers.handleTaskUpdate(
        taskId,
        updates,
        {
          successMessage: `Tche dplace vers ${dropZones.find(zone => zone.statusId === newStatusId)?.title || 'nouveau statut'}`,
          skipApiCall: false // S'assurer que l'appel API est effectu
        }
      );
    }
  };

  // Grer la soumission du formulaire
  const handleFormSubmit = async (updatedTask) => {
    try {
      // Si la tche provient d'un dplacement vers le taskboard '2'
      if (calendarState.taskboardDestination === '2') {
        // Vrifier si un propritaire est assign et si des dates sont dfinies
        const hasOwner = Boolean(updatedTask.resourceId || updatedTask.owner_id);
        const hasDates = Boolean(updatedTask.start || updatedTask.start_date);

        if (!hasOwner || !hasDates) {
          // Rechercher la tche originale pour obtenir son statusId prcdent
          const originalTask = tasks.find(task => task.id.toString() === updatedTask.id.toString());
          const originalStatusId = originalTask?.statusId || '1'; // Fallback  ' faire' si non trouv

          // Utiliser le statut d'origine si diffrent de '2'
          const targetStatusId = originalStatusId === '2' ? '1' : originalStatusId;

          // Mettre  jour avec l'ancien statut et sans dates/propritaire
          updatedTask = {
            ...updatedTask,
            start: null,
            end: null,
            start_date: null,
            end_date: null,
            resourceId: null,
            owner_id: null,
            statusId: targetStatusId,
            description: updatedTask.description,
            extendedProps: {
              ...updatedTask.extendedProps,
              statusId: targetStatusId,
              description: updatedTask.description
            }
          };

          // Utiliser la mthode handleTaskUpdate du hook pour mise  jour et appel API
          await taskHandlers.handleTaskUpdate(
            updatedTask.id,
            updatedTask,
            {
              successMessage: "Tche non modifie - Un propritaire et des dates sont requis pour les tches en cours",
              skipApiCall: false // S'assurer que l'appel API est effectu
            }
          );
        } else {
          // Si les conditions sont remplies, mettre  jour vers le statut '2'
          updatedTask = {
            ...updatedTask,
            statusId: '2',
            extendedProps: {
              ...updatedTask.extendedProps,
              statusId: '2',
              description: updatedTask.description
            }
          };

          // Utiliser la mthode handleTaskUpdate du hook pour mise  jour et appel API
          await taskHandlers.handleTaskUpdate(
            updatedTask.id,
            updatedTask,
            {
              successMessage: "Tche mise  jour avec succs",
              skipApiCall: false
            }
          );
        }
      } else {

        console.log('updatedTask', updatedTask)

        // Prparer les dates inclusives et exclusives
        const startDate = updatedTask.startDate || updatedTask.start;
        const inclusiveEndDate = updatedTask.endDate || updatedTask.end;

        // Calculer la date exclusive (pour FullCalendar)
        let exclusiveEndDate = null;
        if (inclusiveEndDate) {
          // Crer une copie pour ne pas modifier l'original
          if (typeof inclusiveEndDate === 'string') {
            const dateObj = new Date(inclusiveEndDate);
            dateObj.setDate(dateObj.getDate() + 1);
            exclusiveEndDate = dateObj.toISOString().split('T')[0];
          } else {
            const dateObj = new Date(inclusiveEndDate);
            dateObj.setDate(dateObj.getDate() + 1);
            exclusiveEndDate = dateObj;
          }
        }

        // Dterminer si c'est une cration ou une modification
        const isNewTask = !updatedTask.id;
        const isConge = updatedTask.isConge === true;

        if (isNewTask) {
          // CAS DE CRATION
          // Enrichir avec les dates inclusives/exclusives
          const enrichedTask = {
            ...updatedTask,
            start: startDate,
            end: exclusiveEndDate,
            exclusiveEndDate: exclusiveEndDate,
            startDate: startDate,
            endDate: inclusiveEndDate,
            description: updatedTask.description,
            extendedProps: {
              ...(updatedTask.extendedProps || {}),
              inclusiveEndDate: inclusiveEndDate,
              exclusiveEndDate: exclusiveEndDate,
              statusId: updatedTask.statusId,
              isConge: isConge,
              description: updatedTask.description
            }
          };

          // Pour les autres cas, utiliser le gestionnaire normal
          await taskHandlers.handleTaskSubmit(enrichedTask);
        } else {
          // CAS DE MODIFICATION
          // Enrichir avec les dates inclusives/exclusives
          const enrichedTask = {
            ...updatedTask,
            start: startDate,
            end: exclusiveEndDate,
            exclusiveEndDate: exclusiveEndDate,
            description: updatedTask.description,
            extendedProps: {
              ...(updatedTask.extendedProps || {}),
              inclusiveEndDate: inclusiveEndDate,
              exclusiveEndDate: exclusiveEndDate,
              statusId: updatedTask.statusId,
              isConge: isConge,
              description: updatedTask.description,
            }
          };

          // Pour les autres cas, utiliser le gestionnaire normal
          await taskHandlers.handleTaskSubmit(enrichedTask);
        }
      }

      // Rinitialiser l'tat
      setCalendarState(prev => ({
        ...prev,
        taskboardDestination: null,
        taskOriginId: null,
        isFormOpen: false,
        selectedTask: null,
        selectedDates: null
      }));
    } catch (error) {
      console.error('Erreur dans handleFormSubmit:', error);

      // S'assurer que le formulaire est ferm mme en cas d'erreur
      setCalendarState(prev => ({
        ...prev,
        isFormOpen: false,
        taskboardDestination: null,
        taskOriginId: null,
        selectedTask: null,
        selectedDates: null
      }));
    }
  };

  // Hook pour grer les interactions avec les tches
  const taskHandlers = useTaskHandlers(
    setTasks,
    setCalendarState,
    tasks,
    dropZoneRefs,
    dropZones,
    holidays,
    calendarRef
  );

  // Hook pour la navigation dans le calendrier
  const {
    navigateToMonth,
    goToPreviousYear,
    goToNextYear,
    goToPreviousWeek,
    goToNextWeek,
    handleViewChange,
    months
  } = useCalendarNavigation(calendarRef, selectedYear, setSelectedYear);

  return (
    <div className="flex flex-col">
      <div className="w-full calendar">
        <CalendarMain
          calendarRef={calendarRef}
          tasks={tasks}
          resources={resources}
          holidays={holidays}
          taskHandlers={taskHandlers}
          handleViewChange={handleViewChange}
          months={months}
          selectedYear={selectedYear}
          setSelectedYear={setSelectedYear}
          goToPreviousYear={goToPreviousYear}
          goToNextYear={goToNextYear}
          goToPreviousWeek={goToPreviousWeek}
          goToNextWeek={goToNextWeek}    
          navigateToMonth={navigateToMonth}
          dropZoneRefs={dropZoneRefs}
          dropZones={dropZones}

        />
      </div>

      <div className="w-full m-4">
        <TaskBoard
          dropZones={dropZones}
          dropZoneRefs={dropZoneRefs}
          externalTasks={tasks}
          handleExternalTaskClick={taskHandlers.handleExternalTaskClick}
          onDeleteTask={taskHandlers.handleDeleteTask}
          resources={resources}
          onMoveTask={handleMoveTask}
        />
      </div>

      <TaskForm
        isOpen={calendarState.isFormOpen}
        onClose={handleFormClose}
        selectedDates={calendarState.selectedDates}
        selectedTask={calendarState.selectedTask}
        resources={resources}
        statuses={statuses}
        onSubmit={handleFormSubmit}
        isProcessing={calendarState.isProcessing}
      />
    </div>
  );
};